<template>
    <div class="city-item">
        <div class="select-box">
            <h3>按省份选择:</h3>
            <div class="select">
                <ChangeCityItem selectName="省份" :provinceList="provinceList" 
                @resolveChange="changeNoClick"/>
                <ChangeCityItem selectName="城市" :provinceList="cityList" 
                isCity="true" :noClick="clicking" @resolveChange="routerToHomePage"/>
            </div>
        </div>
       <SearchCity/>
    </div>
</template>

<script>
import ChangeCityItem from '@/components/RouterView/changeCity/ChangeCityItem.vue'
import SearchCity from '@/components/RouterView/changeCity/SearchCity.vue'
import {mapMutations} from 'vuex'
export default {
   components:{
       ChangeCityItem,
       SearchCity
      
   },
   methods:{
       changeNoClick(cityName){
           this.clicking = false;
           this.changeCurCity(cityName);
       },
       routerToHomePage(){
           this.$router.push({name:"mainbody"})
       },
       ...mapMutations(['changeCurCity'])
   },
   data(){

       return {
           isProvinceActive:false,
           isCityActive:false,
           clicking:true,
           provinceList:[
               [
                  '山东','甘肃','江苏','北京','云南','海南','浙江','上海','天津','陕西','新疆'
                  ,'贵州'
               ],
               [
                   '安徽','澳门','湖南','河北','香港','辽宁','宁夏','四川','吉林','福建','湖北'
                   ,'广东'
               ],
               [
                   '重庆','山西','江西','黑龙江','青海','河南','台湾','内蒙古','西藏','广西'
               ]
           ],
           cityList:[
               [
                   '北京'
               ]
           ]
       }
   }
}
</script>

<style>

</style>



