<template>
    <div class="changeCity-box">
        <el-row>
            <ChangeCitySelect />
        </el-row>
        <el-row>
            <CityList listTitle="热门城市:" :cityList="HotCityList"/>
        </el-row>
        <el-row>
            <CityList listTitle="最近访问:" :cityList="histroyList"/>
        </el-row>
        <el-row>
            <SpellNav/>
        </el-row>
        <el-row>
            <SpellChange/>
        </el-row>
    </div>
</template>

<script>
import ChangeCitySelect from '@/components/RouterView/changeCity/ChangeCitySelect.vue'
import CityList from '@/components/RouterView/cityList/CityList.vue'
import SpellChange from '@/components/RouterView/spellChange/SpellChange.vue'
import SpellNav from '@/components/RouterView/spellChange/SpellNav.vue'

export default {
  components:{
      ChangeCitySelect,
      CityList,
      SpellChange,
      SpellNav
  },
  data(){
      return {
          isWhich:'',
          HotCityList:['北京','上海','广州','深圳','天津','西安','重庆','杭州','南京','武汉','成都'],
          histroyList:['北京','哈尔滨','固安县','廊坊','大厂回族自治县']
      }
  },
  methods:{
      closeSelect(boolean = false,...index){
        for(let i = 0;i < index.length;i++){
            this.$children[0].$children[0].$children[index[i]].$emit('bridge',false); 
        }  
      }
  },
  created(){
      const that = this;
      var index = 0;
      window.addEventListener('click',function(e){
          
              if(e.target.dataset.type != undefined ){
                  if(that.isWhich == ''){
                     that.isWhich = e.target.dataset.type;
                     console.log('iswhicj赋能完毕')
                  }else{
                      if(that.isWhich == e.target.dataset.type){
                          console.log('iswhich重复')
                         return false;
                  }else{
                that.isWhich = e.target.dataset.type;
                console.log(that.isWhich)
                index = that.isWhich == '省份' ? 1 : 0;
                that.closeSelect(false,index)
                       }
              }
                  
                  }else{
            that.closeSelect(false,0,1);
          }     
      },false);
       
  }
}

</script>

<style lang="less">
 @import url('../../../assets/style/HomePage/changeCity/changeCity.less');
</style>



