<template>
      <div class="search-box select-box">
            <h3>直接搜索:</h3>
            <el-select
    v-model="value"
    filterable
    remote
    reserve-keyword
    placeholder="请输入关键词"
    :remote-method="remoteMethod"
    :loading="loading"
    >
    <el-option
      v-for="item in options"
      :key="item"
      :label="item"
      :value="item">
    </el-option>
  </el-select>
        </div>
</template>

<script>
export default {
  data(){
        return {
              value:'',
              loading:false,
              list:['哈尔滨','北京','上海','天津'],
              options:[]
        }
  },
  methods:{
        remoteMethod(value){
            if(value !== ''){
                //ajax请求
                this.loading = true;
                setTimeout(()=>{
                      this.loading =false;
                      this.options = this.list.filter(item => {
                            return item.indexOf(value) != -1;
                      })
                },1000)
            }else{
              this.options = [];
            }
        }
  }
}
</script>

<style>

</style>





