<template>
    <div class="spellchange-item">
        <a class="english-icon" :name="'city-' + english" :id="'city-' + english">{{english}}</a>
        <div class="spellCity-list">
            <a v-for="item in spellCityList" :key="item" 
                  :class="{hot:item == '北京' || item == '成都' || item == '重庆'}"
                   >
                {{item}}
            </a>
        </div>
    </div>
</template>

<script>
export default {
  props:['english','spellCityList']
}
</script>

<style>

</style>



