<template>
    <div :class="{'province-choose':true,noClick:noClick}" @click="selectCity(true,changeName)" :data-type="selectName"
                              >
                    {{changeName}}
                    <i class="el-icon-caret-bottom"></i>
                    <div class="province-choose-item" v-show="isProvinceBlock">
                        <p v-if="!isCity">省份</p>
                        <p v-else>城市</p>
                        <div class="province-choose-col" v-for="(item,index) in provinceList" :key="index">
                            <span :class='{"province-choose-col-city":true,"active":isClickList[index][dex]}' v-for="(tem,dex) in item" :key="tem"
                                  @click.stop="changeCity(index,dex)">{{tem}}</span>
                        </div>
                    </div>
                </div>
</template>

<script>
export default {
  props:['selectName','provinceList','isCity','noClick'],
   methods:{
        changeCity(index,dex){
           for(let i of this.isClickList){
               i.splice(0);
           }
           this.isClickList[index][dex] = true;
           this.changeName = this.provinceList[index][dex];
           this.selectCity(false);
           this.$emit('resolveChange',this.changeName);
        },
        selectCity(boolean = true,name){
            if(this.noClick){
                return false;
            }
           this.isProvinceBlock = boolean;
        },
        
    },
    created(){
        this.$on('bridge',(val)=>{
            console.log(val);
            this.selectCity(val);
        })
    },
    watch:{
        noClick(n){
            console.log('12313131');
            this.curClickActice = n;
        }
    },
    data(){
        return {
             isClickList:[[],[],[]],
           isProvinceBlock:false,
           changeName:this.selectName,
           curClickActice:this.noClick
          
        }
    }
}
</script>

<style>

</style>



