<template>
    <div class="citylist-item">
        <div class="citylist-box">
            <h3>{{listTitle}}</h3>
            <p>
                <a v-for="(item,index) in cityList" :key="index">
                    {{item}}
                </a>
            </p>
        </div>
    </div>
</template>

<script>
export default {
  props:['listTitle','cityList']
}
</script>

<style lang="less">
  @import url('../../../assets/style/HomePage/cityList/cityList.less');
</style>



