<template>
    <div class="spell-nav">
        <h3>按拼音首字母选择:</h3>
        <p>
            <a v-for="item in spellList" :key="item" :href="'#city-'+ item"
             @click.stop="changeHistroy($event,item)">
                {{item}}
            </a>
        </p>
    </div>
</template>

<script>
export default {
   data(){
      return {
          spellList:['A','B','C','D','E','F','G','H','J','K','L','M','N'
          ,'P','Q','R','S','T','W','X','Y','Z']
      }
  },
  methods:{
      changeHistroy(e,index){
          e.stopPropagation();
          window.location.href = `${e.target.href.replace(e.target.hash,'')}#city-${index}`
         
      }
  }
}
</script>

<style lang="less">
  @import url('../../../assets/style/HomePage/spellchange/spellnav.less');
</style>



